// Interface correspondante
export interface AnnualStatsInterface {
    totalDistance: number[]      // Distance totale en kilomtres
    totalTime    : number[]      // Temps total couru
    totalRuns    : number[]      // Nombre de courses
    averageSpeed : number[]      // Vitesse moyenne
    totalSeconds : number[]      // Nombre total de secondes
    totalMeters  : number[]      // Nombre total de mtres
}

// Classe reprsentant les statistiques annuelles
export class AnnualStatsClass {
    totalDistance: number[]      // Distance totale en kilomtres
    totalTime    : number[]        // Temps total couru
    totalRuns    : number[]      // Nombre de courses
    averageSpeed : number[]      // Vitesse moyenne
    totalSeconds : number[]      // Nombre total de secondes
    totalMeters  : number[]      // Nombre total de mtres

    constructor(){
        this.totalDistance = [];
        this.totalTime     = [];
        this.totalRuns     = [];
        this.averageSpeed  = [];
        this.totalSeconds  = [];
        this.totalMeters  = [];
    }
}